.parent{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
}

.hero{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    /* padding-top: 16rem; */
}

.heroImage{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.hero>h1{
    font-size: 60px;
    background-image: -webkit-linear-gradient(#3449BE, #FF92F3);
    background-image: linear-gradient(to right,#3449BE, #FF92F3);
    -webkit-background-clip: text;
    background-clip: text;
    font-family: 'bold';
    margin: 0;
    -webkit-text-stroke: 5px transparent;
}

.hero>p{
    color: #fff;
    font-size: 30px;
    width: 100%;
    text-align: center;
    padding: 0px 30%;
    font-family: 'light';
    z-index: 1;
}


.btnContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
    margin-top: 2rem;
}

.postJobBtn{
    color: #fff;
    font-family: 'light';
    font-size: 40px;
    background-image: linear-gradient(to right, #2c2231, #171d32);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    z-index: 1;
    padding: 25px 50px;/*i used 25 to make up the 5px border in the other button*/
}

.viewListingBtn{
    color: #fff;
    font-family: 'light';
    font-size: 40px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    z-index: 1;
    background-color: transparent;
    border: 5px solid #2c2231;
    padding: 20px 50px;
}

.jobListingParent{
    padding-top: 3rem;
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(to right, #171d32, #2c2231);
}

.listingTitle{
    font-family: 'bold';
    font-size: 40px;
    color: #fff;
}

@media screen and (max-width: 1000px) {
    .hero>p{
        padding: 0px 5%;
    }
    
    .postJobBtn{
        font-size: 30px;
        cursor: none;
        padding: 25px 10px;
    }

    .viewListingBtn{
        font-size: 30px;
        cursor: none;
        padding: 20px 10px;
    }
}

@media screen and (max-width: 770px) {
.hero{
    padding-top: 10rem;
}

 .hero>h1{
    width: 100%;
    text-align: center;
}

.hero>p{
    padding: 0px 10px;
    width: 100%;
    text-align: center;
}

.btnContainer{
    flex-direction: column;
    -ms-flex-direction: column;
    row-gap: 20px;
    width: 100%;
}


.postJobBtn{
    font-size: 20px;
    cursor: none;
    padding: 25px 0px;
    width: 80%;
}

.viewListingBtn{
    font-size: 20px;
    cursor: none;
    padding: 20px 0px;
    width: 80%;
}
}