.parent{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .05);
    display: flex;
    z-index: 1000;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
}

.box{
    position: absolute;
    width: fit-content;
    width: 50%;
    height: fit-content;
    padding: 20px 10px 40px 10px;
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
    background-image: linear-gradient(to top right,#302044, #000000, #2F1818);
    border-radius: 20px;
}

.header{
    width: 100%;
    /* padding: 10px; */
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.cancelContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    background-color: #fff;
    border-radius: 50%;
    cursor: pointer;
}

.cancelContainer>img{
    width: 10px;
    height: 10px;
}

.body{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0px;
}

.body>p{
    color: #fff;
    font-family: 'light';
    font-size: 20px;
}

.btnContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
}

.metaBtn{
    background-color: #fff;
    color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 5px;
    font-family: 'light';
    font-size: 25px;
    padding: 20px 20px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

.metaBtn:hover{
    background-color: #ebebeb;
}

.metaBtn>img{
    width: 30px;
    height: 30px;
}

.successContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    -ms-flex-direction: column;
}

.successContainer>img{
    width: 170px;
    height: 170px;
}

.successContainer>h1{
    color: #fff;
    font-family: 'bold';
    margin: 0;
    font-size: 2rem;
    margin-top: 20px;
}

.successContainer>p{
    color: #dddddd;
    font-family: 'light';
    margin: 0;
    font-size: 1rem;
    margin-top: 20px; 
}

.jobsBtnContainer{
    width: 100%;
    height: 100%;
    display: flex;
    column-gap: 20px;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.browseBtn{
    background-image: linear-gradient(to right,#111C33, #2E2230);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 5px;
    font-family: 'light';
    font-size: 25px;
    /* padding: 1rem 1rem; */
    width: 15rem;
    height: 5rem;
    border: none;
    border-radius: 10px;
    cursor: pointer; 
}

.browseBtn:hover{
    background-image: linear-gradient(to right,#2E2230, #131C33); 
}

.postBtn{
    background-image: linear-gradient(to right,#131C33, #2E2230);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 5px;
    font-family: 'light';
    font-size: 25px;
    width: 15rem;
    height: 5rem;
    border: none;
    border-radius: 10px;
    cursor: pointer;   
}

.postBtn:hover{
    background-image: linear-gradient(to right,#2E2230, #131C33); 
}

@media screen and (max-width: 770px) {
    .box{
        width: 95%;
        /* height: 70vh; */
    }
    .body{
        padding: 4rem 10px;
    }
    
    .body>p{
        text-align: center;
        width: 100%;
    }

    .metaBtn{
        cursor: none;
    }
    
    .jobsBtnContainer{
        flex-direction: column;
        -ms-flex-direction: column;
        row-gap: 20px;
    }

    .browseBtn{
        /* width: 10rem; */
        cursor: none; 
    }
    
    .postBtn{
        /* width: 10rem; */
        cursor: none;  
        border: 2px solid #302044;
        background-image: unset;
        background-color: transparent;
    }

    .successContainer>p{ 
        text-align: center;
    }
}