.parent{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .05);
    display: flex;
    z-index: 1000;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
}

.box{
    width: 60vw;
    height: 80vh;
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-image: linear-gradient(to top right,#302044, #000000, #2F1818);
    border-radius: 20px;
    padding: 30px;
}

.thumbsIcon{
    width: 200px;
    height: 200px;
    filter: invert(1);
}

.box>h1{
    font-family: 'bold';
    font-size: 30px;
    color: #fff;
    margin: 0;
    margin: 20px;
}

.box>p{
    font-family: 'light';
    font-size: 25px;
    color: #fff;
    margin: 0; 
}

.box>button{
    background-image: linear-gradient(to right,#111C33, #2E2230);
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 5px;
    font-family: 'light';
    font-size: 25px;
    /* padding: 1rem 1rem; */
    width: 15rem;
    height: 5rem;
    border: none;
    border-radius: 10px;
    cursor: pointer; 
    margin-top: 50px; 
}

@media screen and (max-width: 770px) {
    .box{
        width: 95%;
        height: 70vh;
    }

    .box>h1{
        text-align: center;
        width: 100%;
    }
    
    .box>p{
        text-align: center;
        width: 100%;
    }

    .box>button{
        cursor: none;
    }
}