.header{
   width: 100vw;
   height: fit-content;
   display: flex;
   flex-direction: column;
   position: fixed;
   top: 0;
   z-index: 100;
   box-sizing: border-box;
}

.topHeader{
   background-color: #D9D9D9;
   min-height: 40px;
   width: 100%;
   display: flex;
   justify-content: flex-end;
   align-items: center;
   padding-left: 30px;
   padding-right: 30px;
}

.topCenter{
   display: flex;
   width: 33.33%;
   width: max-content;
   min-height: 100%;
   font-family: 'light';
}


.topIconImage{
  display: flex;
  width: 33.33%;
  min-height: 100%;
  column-gap: 15px;
  justify-content: flex-end;
  align-items: center;
  padding-right: 20px;
}

.topIconImage>img{
   width: 15px;
   height: 15px;
}

.centerHeader{
   display: flex;
   width: 100%;
   min-height: 60px;
   background-color: #000;
   padding-left: 30px;
   padding-right: 30px;
}

.logoContainer{
    width: 50%;
    min-height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.logoContainer>img{
   width: 60px;
   height: 60px;
   margin-right: 30px;
}

.logoContainer>h1{
   font-family: 'bold';
   color: #fff;
   font-size: 30px;
   margin: 0;
}

.eventSideContainer{
   width: 50%;
    min-height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    column-gap: 20px;
}

.dropdown{
   display: flex;
   justify-content: flex-start;
   align-items: center;
   width: fit-content;
   padding: 5px;
   cursor: pointer;
   position: relative;
}

.dropdown>p{
   margin: 0;
   font-family: 'light';
   color: #fff;
   font-size: 18px;
}

.dropdown>img{
   filter: invert(1);
   width: 10px;
   height: 10px;
   margin-left: 6px;
}

.bottomHeader{
   width: 100%;
   min-height: 60px;
   max-height: fit-content;
   display: flex;
   background-image: linear-gradient(to right, #2c2231, #171d32);
   padding: 0px 10px;
   padding-left: 30px;
   padding-right: 30px;
}

.bottomLeftContainer{
   width: 70%;
   min-height: 100%;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   column-gap: 20px;
}

.howToAdd{
   display: flex;
   justify-content: flex-start;
   align-items: center;
   column-gap: 10px;
}

.howToAdd>img{
   width: 20px;
   height: 20px;
   filter: invert(1);
   /* margin-right: 20px; */
}

.howToAdd>p{
   color: #fff;
   font-family: 'light';
   font-size: 17px;
   margin: 0;
}

.cmpPortion{
   background-color: #fff;
   border-radius: 5px;
   padding: 0px 5px;
   width: max-content;
   min-height: 30px;
   max-height: 30px;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   cursor: pointer;
   /* position: relative; */
}

.cmpPortion>p{
   margin: 0;
   font-family: 'bold';
   font-size: 20px;
}

.circle{
   border-radius: 50%;
   min-width: 40px;
   max-width: 40px;
   min-height: 40px;
   max-height: 40px;
   background-color: #fff;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-right: -10px;
}

.circle>img{
   width: 17px;
   height: 17px;
}

.bottomRightContainer{
   width: 30%;
   min-height: 100%;
   display: flex;
   justify-content: flex-end;
   align-items: center;
}

.bottomRightContainer>p{
   margin: 0;
   font-size: 17px;
   font-family: 'light';
   color: #fff;
}

.getSomeBtn{
   background-color: #fff;
   padding: 7px 10px;
   border-radius: 5px;
   color: #000;
   font-family: 'bold';
   font-size: 20px;
   margin-left: 20px;
   cursor: pointer;
   border: none;
}

.getSomeBtn:hover{
   background-color: #e5e5e5;
}

@media screen and (max-width: 1060px) {
.bottomHeader{
      padding-top: 5px;
      padding-bottom: 5px;
      row-gap: 10px;
      min-height: fit-content;
}

.bottomLeftContainer{
   flex-direction: column;
   justify-content: center;
   align-items: flex-start;
   width: 50%;
}

.bottomRightContainer{
   width: 50%;
}
}

@media screen and (max-width: 770px) {
.header__{
   width: 100vw;
   min-height: fit-content;
   max-height: 100vh;
   display: flex;
   flex-direction: column;
   position: fixed;
   top: 0;
   z-index: 100;
   box-sizing: border-box;
   background-color: #000;
   flex-direction: column;
   -ms-flex-direction: column;
   padding-bottom: 10px;
}

.topHeader__{
   background-color: #000;
   min-height: 5vh;
   width: 100%;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding-left: 20px;
   padding-right: 20px;
}

.logoPart__{
   display: flex;
   justify-content: flex-start;
   align-items: center;
   column-gap: 20px;
   width: 50%;
}

.logoPart__>img{
   width: 50px;
   height: 50px;
}

.logoPart__>h1{
   margin: 0;
   font-size: 30px;
   color: #fff;
   font-family: 'bold';
}

.hamburger__{
   width: 50%;
   display: flex;
   justify-content: flex-end;
   align-items: center;
}

.hamburgerIcon{
   width: 25px;
   height: 25px;
   filter: invert(1);
}

.cancelIcon{
   width: 20px;
   height: 20px;
   filter: invert(1);
}

.bottomLeftContainer{
   display: flex;
   min-width: 100%;
   height: 10vh;
   padding-left: 20px;
   padding-top: 10px;
   row-gap: 10px;
}

.bottomRightContainer{
   width: 100%;
   height: 10dvh;
   padding-left: 20px;
   padding-top: 10px;
   justify-content: flex-start;
   padding-bottom: 20px;
}
.getSomeBtn{
   padding: 5px 10px;
   font-size: 18px;
}

.cmpPortion>p{
   margin: 0;
   font-family: 'bold';
   font-size: 15px;
}

.eventSideContainer{
      width: 100%;
      min-height: 100%;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      -ms-flex-direction: column;
      row-gap: 20px;
      padding: 10px 20px;
      height: calc(100vh - 37vh);
      position: relative;
      background-image: linear-gradient(to right, #2c2231, #171d32);
}

.frameImage{
   position: absolute;
   content: '';
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   width: 100%;
   height: 100%;
   object-fit: cover;
   /* z-index: 1; */
   /* background-color: red; */
}

.footer{
   width: 100%;
   height: 10dvh;
   background-color: #D9D9D9;
   display: flex;
   justify-content: flex-start;
   align-items: center;
   flex-direction: column;
   -ms-flex-direction: column;
   padding: 0px 20px;
   row-gap: 15px;
}

.bottomTxt{
   width: 100%;
   margin: 0;
   font-family: 'bold';
   height: fit-content;
   text-align: center;
}

.topIconImage{
   width: 100%;
   min-height: fit-content;
   justify-content: center;
   align-items: center;
   column-gap: 30px;
   padding-right: 0px;
   }
   
   .topIconImage>img{
      width: 18px;
      height: 18px;
   }

   .eventsTitle{
   display: flex;
   justify-content: flex-start;
   align-items: center;
   }

   .dropdown{
   display: flex;
   justify-content: flex-start;
   align-items: flex-start;
   flex-direction: column;
   -ms-flex-direction: column;
   cursor: none;
}

.eventsTitle>p{
   margin: 0;
   font-family: 'bold';
   color: #fff;
   font-size: 18px;
}

.eventsTitle>img{
   filter: invert(1);
   width: 10px;
   height: 10px;
   margin-left: 6px;
}
    
}